@import url(https://fonts.googleapis.com/css2?family=Arizonia&display=swap);
body{margin:0;padding:0;font-family:'Montserrat', sans-serif !important;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}*{box-sizing:border-box}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}body::-webkit-scrollbar{width:8px}body::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}body::-webkit-scrollbar-thumb{background-color:#aeadb1;border-radius:6px}

.nav-color{background-color:#fff;z-index:2;font-size:19px}.text-boldest{font-weight:900}.color-custom-blue{color:#091292 !important}.color-custom-grey{color:#8d81a5}.bg-custom-blue{background-color:#091292 !important}.nav-link{min-width:100px}.nav-link:hover{font-weight:bold}.skills-img{width:80px;transition:width 0.15s;transition-timing-function:ease-in-out;cursor:pointer}.skills-img:hover{width:90px;-webkit-filter:none;filter:none}.justify-content-evenly{justify-content:space-evenly}.proj-image{border-radius:20px}.proj-opacity{-webkit-animation:opacityChange 2s;animation:opacityChange 2s}.text-link,.text-link:hover{color:#fff;text-decoration:none}.contact-btns span{max-width:0;transition:max-width 1s;display:inline-block;vertical-align:top;white-space:nowrap;overflow:hidden}.contact-btns:hover span{max-width:7rem}.contact-btns:hover span::before{content:'|';padding-left:5px;padding-right:5px;color:#ffffff}.particle-container{position:relative;background-color:#fff;z-index:-1}.color-name-blue{color:#000a94 !important}.icon-card{height:100px;width:90px}.contact-img{width:35%}.contact-card,.skill-icons-container{-webkit-animation:slidein 1.5s;animation:slidein 1.5s}.proj-slide{-webkit-animation:slidein 2s;animation:slidein 2s}.contact-card:hover{cursor:pointer;box-shadow:1px 1px 7px grey !important}.contact .card{border:none !important}.about-text{letter-spacing:1px;font-size:18px;color:#727070}.vertical-timeline::before{background:#bebebe !important}.about-me-block{font-family:Fira Sans, sans-serif;font-weight:700;font-size:5.5rem;line-height:120px;opacity:0.1;color:#727070;text-transform:uppercase;text-align:right}.timeline-work-icon{left:26px !important}.timeline-grad-icon{left:23px !important}.home-container{position:absolute;top:0;left:0;right:0;z-index:1}.resume-btn:hover{background-image:linear-gradient(to top right, #091292 !important, #2631d4)}.bipin-signature{font-family:'Arizonia', cursive;font-size:2.7rem;color:#191e69}.bipin-signature:hover{color:#091292 !important}.dashed-border{border-style:dashed}.programmer{width:50%}.page-not-found{display:flex;align-items:center;justify-content:center;flex-flow:row wrap;font-weight:bold;margin-top:13%}@-webkit-keyframes slidein{from{margin-top:10%;opacity:0}to{margin-top:0%;opacity:1}}@keyframes slidein{from{margin-top:10%;opacity:0}to{margin-top:0%;opacity:1}}@-webkit-keyframes slideout{from{margin-top:-2%;opacity:0}to{margin-top:0%;opacity:1}}@keyframes slideout{from{margin-top:-2%;opacity:0}to{margin-top:0%;opacity:1}}@-webkit-keyframes opacityChange{from{opacity:0}to{opacity:1}}@keyframes opacityChange{from{opacity:0}to{opacity:1}}@media only screen and (min-width: 320px) and (max-width: 1168px){.timeline-work-icon{left:16px !important}.timeline-grad-icon{left:13px !important}.programmer{width:100%}.page-not-found{margin-top:22%}}@media only screen and (min-width: 768px){.nav-slide{-webkit-animation:slideout 1.5s;animation:slideout 1.5s}}

